body {
    font-family: "Dosis",sans-serif;
    padding-bottom: 70px;
}

.navbar-nav {
    float: none;
    margin: 0 auto;
    display: block;
    text-align: center;
}

.navbar-nav > li {
    display: inline-block;
    float: none;
}


.sliding-middle-out {
    display: inline-block;
    position: relative;
    padding-bottom: 3px;
}
.sliding-middle-out:after {
    content: '';
    display: block;
    margin: auto;
    height: 1px;
    width: 0px;
    background: transparent;
    transition: width .5s ease, background-color .5s ease;
}
.sliding-middle-out:hover:after {
    width: 100%;
    background: white;
}

.avatar {
    vertical-align: middle;
    width: 80%;
    height: auto;
    border-radius: 50%;
    max-width: 400px;
}

.rbutton {
    border: 2px solid white;
    border-radius: 50%;
    margin: 5px;
    width: 40px;
}

